<template>
  <div class="row">
    <div class="col s12 s12 l6 push-l3">
      <div id="modalEdicao" class="modal">
        <p class="center red-text"><b>Formulário de Edição</b></p>
        <div class="modal-content">
          <form @submit.prevent="editar()">
            <div class="input-field col s12 l6">
              <i class="material-icons prefix">chevron_right</i>
              <input
                type="text"
                v-model="dadosEdicao.placa"
                name="placa"
                id="placa"
                required
              />
              <label class="active" for="placa">Placa</label>
            </div>
            <div class="input-field col s12 l6">
              <i class="material-icons prefix">chevron_right</i>
              <input
                type="text"
                v-model="dadosEdicao.chassi"
                name="chassi"
                id="chassi"
                required
              />
              <label class="active" for="chassi">Chassi</label>
            </div>
            <div class="input-field col s12">
              <i class="material-icons prefix">chevron_right</i>
              <input
                type="text"
                v-model="dadosEdicao.renavam"
                name="renavam"
                id="renavam"
                required
              />
              <label class="active" for="renavam">Renavam</label>
            </div>
            <div class="input-field col s12">
              <i class="material-icons prefix">chevron_right</i>
              <input
                type="text"
                v-model="dadosEdicao.modelo"
                name="modelo"
                id="modelo"
                required
              />
              <label class="active" for="modelo">Modelo</label>
            </div>
            <div class="input-field col s12 l6">
              <i class="material-icons prefix">chevron_right</i>
              <input
                type="text"
                v-model="dadosEdicao.marca"
                name="marca"
                id="marca"
                required
              />
              <label class="active" for="marca">Marca</label>
            </div>
            <div class="input-field col s12 l6">
              <i class="material-icons prefix">chevron_right</i>
              <input
                type="text"
                v-model="dadosEdicao.ano"
                name="ano"
                id="ano"
                required
              />
              <label class="active" for="ano">Ano</label>
            </div>
            <div class="col s12">
              <button type="submit" class="btn-small">
                editar <i class="material-icons right">edit</i>
              </button>
            </div>
          </form>
        </div>
        <div class="modal-footer">
          <a class="modal-close waves-effect waves-green btn-flat right">Close</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import M from 'materialize-css'
export default {
  name: "ModalEdicao",
  props: {
    dadosEdicao: {
      id: null,
      placa: "",
      chassi: "",
      renavam: "",
      modelo: "",
      marca: "",
      ano: "",
    },
  },
  data() {
    return {};
  },
  mounted(){
      M.AutoInit()
  },
  methods: {
    editar() {
      fetch(`http://localhost:3000/${this.dadosEdicao.id}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(this.dadosEdicao),
      })
        .then((res) => res.json())
        .then((data) => {
          if (data.msg) {
            alert("Editado com Sucesso !:-)");
            this.dadosEdicao = {
              id:null,
              placa: "",
              chassi: "",
              renavam: "",
              modelo: "",
              marca: "",
              ano: "",
            };
            this.$router.push({ name: "Veiculos" });
            window.location.reload();
          } else {
            alert("Desculpe Houve um Erro :-(");
          }
        })
        .catch((err) => {
          alert("Desculpe Houve um Erro :-(");
          console.log(err);
        });
    },
  },
};
</script>

