<template>
  <div class="row">
    <div class="col s12 l10 push-l1">
      <p class="center"><b>Tabela de Veículos</b></p>
      <table class="responsive-table centered striped highlight">
        <thead>
          <tr>
            <th>Id</th>
            <th>Placa</th>
            <th>Chassi</th>
            <th>Renavam</th>
            <th>Modelo</th>
            <th>Marca</th>
            <th>Ano</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(vc, index) in veiculos" :key="index">
            <td>{{ vc.id }}</td>
            <td>{{ vc.placa }}</td>
            <td>{{ vc.chassi }}</td>
            <td>{{ vc.renavam }}</td>
            <td>{{ vc.modelo }}</td>
            <td>{{ vc.marca }}</td>
            <td>{{ vc.ano }}</td>
            <td>
              <a class="btn-small btn-floating red"
               @click="deletar(vc)"
                style="margin-right:5px;"
                >
                <i class="material-icons">delete</i>
              </a>
              
              <a class="btn-small btn-floating modal-trigger blue"
               href="#modalEdicao"
               @click="editar(vc)"
               style="margin-right:5px;"
              >
              <i class="material-icons">edit</i>
              </a>
              
              <a class="btn-small btn-floating modal-trigger orange"
               href="#modalDetails"
               @click="detalhes(vc)"
              >
              <i class="material-icons">visibility</i>
              </a>
            </td>
          </tr>
        </tbody>
      </table>
      <ModalDetails :dados="dados"></ModalDetails>
      <ModalEdicao :dadosEdicao="dadosEdicao"></ModalEdicao>

    </div>
  </div>
</template>

<script>
import ModalDetails from '../components/ModalDetails.vue';
import ModalEdicao from '../components/ModalEdicao.vue';
import M from 'materialize-css';
export default {
  name: "Veiculos",
  data() {
    return {
      veiculos: [],
      dadosEdicao:{},
      dados: {
        id: null,
        placa: "",
        chassi: "",
        renavam: "",
        modelo: "",
        marca: "",
        ano: "",
      },
    };
  },
  mounted() {
    M.AutoInit()
    fetch("http://localhost:3000/", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(JSON.stringify(data));
        this.veiculos = data;
      })
      .catch((err) => {
        alert("Desculpe Houve um Erro !:-(");
        console.log(err);
      });
  },
  methods: {
    deletar(vc) {
      if (confirm("Deseja realmente excluír?")) {
        fetch(`http://localhost:3000/${vc.id}`, {
          method: "DELETE",
          headers: {
            "Content-Type": "application/json",
          },
        })
          .then((res) => res.json())
          .then((data) => {
            if(data.msg){
               window.location.reload();
            }
            
          })
          .catch((err) => {
            alert("Desculpe Houve um Erro !:-(");
            console.log(err);
          });
      }else{
          return false
      }
    },
    editar(vc){
        fetch(`http://localhost:3000/${vc.id}`,{
          method:'GET',
          headers:{
            'Content-Type':'application/json'
          }
        })
        .then(resp => resp.json())
        .then(data =>{
          this.dadosEdicao = data[0]
          
        })
        .catch(err => console.log(`Erro: ${err}`))
    },
    detalhes(vc){
       this.dados = vc
    }
  },
  components:{
    "ModalDetails":ModalDetails,
    "ModalEdicao":ModalEdicao,
  }
};
</script>

<style scoped>
   #modalEdicao{
     background-color: blue !important;
   }
</style>
